.socials {
  // display: none;
  position: absolute;
  z-index: 7500;
  bottom: 49rem;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  will-change: opacity;
  transition: opacity 1s ease-in-out;

  &--active {
    opacity: 1;
  }

  @media (min-width: 1024px) {
    position: static;
    transform: translateX(0);
    opacity: 1;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__item {
    margin-right: 2.5rem;

    &:nth-of-type(2) {
      margin-right: 0;

      @media (min-width: 1024px) {
        margin-right: 2.5rem;
      }
    }

    a {
      display: block;
      width: 3.6rem;
      height: 3.6rem;

      img {
        width: 3.6rem;
        height: 3.6rem;
      }
    }
  }
}